.dropdown {
  position: relative;
  display: inline-block;
  margin-right: 2rem;
}
.dropdown ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  top: 45px;
  right: 0px;
  width: 200px;
  background-color: white;
  font-weight: bold;
  position: absolute;

  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown li div {
  color: #000;
  text-decoration: none;
}
.dropdown li {
  margin: 0;
  padding: 6px 4px;
  border-bottom: 1px solid #e5e5e5;
}
.dropdown li:last-child {
  margin-right: 0;
}

.dropdown li:hover {
  background-color: #e5e5e5;
  color: white;
}
.dropdown .button {
  width: 178px;
  height: 18px;
  background-color: #f8ea8c;
  padding: 12px;
  border-radius: 5px;
  font-weight: bold;
  color: #00aba8;
}
.dropdown .button:before {
  content: "";
  position: absolute;
  width: 0px;
  height: 0px;
  border: 10px solid;
  border-color: white transparent transparent transparent;
  right: 6px;
  top: 18px;
}
